}

.fundo2{

    
    background: 
    -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(12, 20, 88, 0.459)), color-stop(59%, rgba(0, 0, 0, 0)), color-stop(100%, rgba(11, 60, 92, 0.329))),url("/static/media/bruno3 (1 of 1).8ca70177.jpg") no-repeat center center fixed;
    
    
